:root {
  --color-black: #000;
  --color-white: #fff;
}

@font-face {
  font-family: "gilroy-bold";
  src: url("../fonts/gilroy-bold.woff2") format("woff2"), url("../gilroy-bold.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "gilroy-medium";
  src: url("../fonts/gilroy-medium.woff2") format("woff2"), url("../gilroy-medium.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

